@font-face {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-stretch: 100%;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/opensans/v34/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsjZ0B4gaVI.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
.appContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    

}
html {
    font-family: 'Open Sans';
    scroll-behavior: smooth;
}

body {
    margin: 0;
}

.header {
    overflow: hidden;
    background-color: #d1e0d1;
    padding: 0.3%;
    max-width: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    max-height: 6vh;
    position: fixed;
    top: 0;
    box-sizing: border-box;
}

.headerLeft {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
}

.headerText {
    color: black;
    text-align: center;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.headerText:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.1);
} 

@keyframes pulse {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

.kijijiLogo {
    height: 70%;
    max-height: 50px;
    width: auto;
    pointer-events: none;
}

.grasshopperLogo {
    height: 100%;
    max-height: 70px;
    width: auto;
}

.kijijiLogo path {
    fill: #383272;

}

.iconContainer svg {
    pointer-events: all;
}

.kijijiLogo:hover {
    cursor: pointer;
    animation: pulse 2s infinite;
}

.kijijiLogo:hover path {
    cursor: pointer;
}

.headerLinks {
    display: flex;
    flex-direction: row;
    flex-grow: 1;

}

.headerLinks>div {
    flex: 1;
}

.headerTitle {
    font-weight: bold;
    font-size: 120%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.aboutSection,
.pricingSection,
.contactSection,
.gallerySection {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.aboutContainer, 
.pricingContainer,
.contactContainer {
    font-size: 100%;
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media screen and (min-width: 768px) {
    .aboutContainer, 
.pricingContainer,
.contactContainer {
    font-size: 150%;
}
}


.logoContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 1%;
    padding-right: 10%;

}
.footerSection {
    height: 10vh;
    background-color: #d1e0d1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100% !important;
    font-size: 80%;

}

.iconContainer {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contentContainer {
    background-color: rgb(240, 250, 236);
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 0;
    flex-wrap: wrap;
    justify-content: space-evenly;
    max-width: 100%;
    

}

.clickableText:hover {
    cursor: pointer;
}
.clickableSVG:hover {
    cursor: pointer;
}

.grassCutPreviewContainer>img {
    max-width: 100%;
    object-fit: cover;
    display: flex;
    justify-content: center;
    align-items: center;

}

.aboutContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20%;
}
.pricingContainer{
    margin-bottom: 20%;
}
.contactContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 20%;
}
.galleryContainer {
    margin-bottom: 20%;
}
.contactContainer li {
    text-align: left;
}

.emailContainer {
    font-size: 130%;
    color: rgb(113, 114, 167);
    /* background: #f1f1f1; */
    margin: 1%;
    padding: 1%;
    text-decoration: none;
    white-space: pre-wrap; /* CSS3 */    
    white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
    white-space: -pre-wrap; /* Opera 4-6 */    
    white-space: -o-pre-wrap; /* Opera 7 */    
    word-wrap: break-word; /* Internet Explorer 5.5+ */
}

@media screen and (max-width: 1024px) {
    .aboutSection,
    .pricingSection,
    .contactSection{
        max-width: 100%;
        width: 100%; 
    }
  }

  @media screen and (max-width: 515px) {
    .headerLinks{
        display: none; 
    }
    .logoContainer{
        flex: 1;
    }
  }
.aboutHeroImageContainer{
    background-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url("https://i.imgur.com/0SWbRRa.jpeg");
    min-height: 100vh;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.heroText{
    font-size: 250%;
    flex: 1;
    color: white;
    white-space: pre-wrap; /* CSS3 */    
 
}
.heroTextContainer{
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.contactButton{
    background-color: #d1e0d1;
    color: rgb(0, 0, 0);
    border: none;
    padding: 1%;
    font-size: 120%;
    border-radius: 5px;
    margin: 1%;
}
.contactButton:hover{
    cursor: pointer;
    background-color: #b2bdb2;
    
}
.sectionHeader {
    font-size: 350%;
    font-weight: bold;
    margin-top: 1%;
    margin-bottom: 1%;
}
.galleryImageContainer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .galleryImage {
    max-width: 100%;
    height: auto;
  }
